import React from 'react'
import { connect } from 'react-redux';
import { getFriends, getFriendRequests, removeFriend, acceptRequest, rejectRequest } from '../actions/accordionsAction';
import PropTypes from 'prop-types';
class Accordions extends React.Component {

    componentDidMount() {
        this.props.getFriends();
        this.props.getFriendRequests();
    }
    
    removeFriendClicked = (id) => {
        this.props.removeFriend(id);        
    }
    acceptRequestClicked = (id) => {
        this.props.acceptRequest(id);    
    }
    rejectRequestClicked = (id) => {
        this.props.rejectRequest(id);    
    }

    render() {

        const friends_list  =  this.props.friends.map((item,key)=>{
            return(
                <React.Fragment key = {key}>
                    <div className="friends_paragraph">
                        <img className="friends_image" src={item.image} alt="avatar" />
                        &nbsp;{item.name}
                        <br />
                        <button type="button" className="btn btn-danger" onClick={()=>this.removeFriendClicked(item.id)}>لغو دوستی</button>
                    </div>
                {this.props.friends.length-1 !== key ? <hr className="friends_seperator" /> : null} 
                </React.Fragment>    
            );
        });  
        
         
        const friend_requests_list  =  this.props.friend_requests.map((item,key)=>{
            return(
                <React.Fragment key={key}>
                    <div className="friends_paragraph" >
                        <img className="friends_image" src={item.image} alt="avatar" />
                        &nbsp;{item.name}
                        <br />
                        <div className="accept_reject_btns_div">
                            <abbr title="تایید پیشنهاد دوستی"><button type="button" className="btn btn-success" onClick={()=>this.acceptRequestClicked(item.id)} >✓</button></abbr>
                            &nbsp;
                            <abbr title="رد پیشنهاد دوستی"><button type="button" className="btn btn-danger" onClick={()=>this.rejectRequestClicked(item.id)} >X</button></abbr>
                        </div>
                    </div>
                    {this.props.friend_requests.length-1 !== key ? <hr className="friends_seperator" /> : null} 
                </React.Fragment>    
            );
        });  

        return (
            <div className="accordion" id="accordionExample">
                <div className="card">
                    <div className="card-header card-header-leftbox" id="headingOne">
                    <h2 className="mb-0">
                        <button className="btn btn-link collapse_btn" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                             لیست دوستان <span className="badge badge-warning text-secondary badge-span-leftbox">{this.props.friends.length}</span>
                        </button>
                    </h2>
                    </div>
                    <div id="collapseOne" className="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
                    <div className="card-body left_boxes">
                        
                        {friends_list}                      
                    
                    </div>
                    </div>
                </div>

                <div className="card">
                    <div className="card-header card-header-leftbox" id="headingTwo">
                    <h2 className="mb-0">
                        <button className="btn btn-link collapsed collapse_btn" type="button" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                            درخواست های دوستی  <span className="badge badge-warning text-secondary badge-span-leftbox">{this.props.friend_requests.length}</span>
                        </button>
                    </h2>
                    </div>
                    <div id="collapseTwo" className="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">

                    <div className="card-body left_boxes">

                        {friend_requests_list}
                    
                    </div>
                    </div>
                </div>


            </div>
        )
    }
}


Accordions.propTypes = {
    friends: PropTypes.array.isRequired,
    friend_requests: PropTypes.array.isRequired
}


const mapStateToProps = state => ({
    friends: state.accordionInfo.friends,
    friend_requests: state.accordionInfo.friend_requests
})

export default connect(mapStateToProps, {getFriends, getFriendRequests, removeFriend, acceptRequest, rejectRequest})(Accordions);