const initialState = {
    items: [],
    interested_people: [],
    pageNumber: 1,
    ppp: 5
}

export default function(state = initialState, action){
    switch(action.type){

        case 'POSTS_LIST':
            return {
                ...state,
                items: action.payload
            }
        case 'LIKE_POST':
                state.items = state.items.map(post=>{
                    if(post.id===action.id){
                       post.likes = (parseInt(post.likes)+1).toString();
                       post.you_liked_it = true;
                       return post;
                    }
                    else{
                       return post;
                    }
                })
                return {...state};
        case 'UNLIKE_POST':
                state.items = state.items.map(post=>{
                    if(post.id===action.id){
                        post.likes = (parseInt(post.likes)-1).toString();
                        post.you_liked_it = false;
                        return post;
                    }
                    else{
                        return post;
                    }
                })
                return {...state};     
                
            case 'DELETE_POST':
                state.items =  [...state.items.filter((post)=>post.id !== action.id )];
                return {...state};
                
            case 'INTERESTED_LIST':
                return {
                    ...state,
                    interested_people: action.payload
                }   
            case 'REQUEST_PAGE_NUMBER':
               return {
                ...state,
                pageNumber: action.payload
               } 
        default:
            return state;
    }

}