import React from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { searchUsers, getCurrentUser, acceptCommand, rejectCommand, doRequestCommand, cancelRequestCommand } from '../actions/navActions';
import plusSign from '../icons/plus.svg';
import magnifierSign from '../icons/magnifier.svg';
import tickSign from '../images/tick.svg';
import crossSign from '../images/cross.svg';
import dropdownImg from '../images/Dropdown.png';


class Nav extends React.Component {


    componentDidMount(){
        this.props.getCurrentUser();
    }

    searchClicked = (event) => {
        event.preventDefault();
        this.props.searchUsers(event.target.users_name.value);
    }

    rejectRequest = (key) => {
        this.props.rejectCommand(key);
    }

    acceptRequest = (key) => {
        this.props.acceptCommand(key);
    }

    doRequest = (key) => {
        this.props.doRequestCommand(key);
    }

    cancelRequest = (key) => {
        this.props.cancelRequestCommand(key);
    }

    render() {
        let buttons = null;
        const users =
        this.props.users.map((user,key)=>{
            switch(user.friendship_state){
                case 'friend':
                    buttons = <span className="text-info"> دوست شما</span>
                    break;

                case 'requested':
                    buttons = 
                    <React.Fragment>
                        <abbr title="تایید پیشنهاد دوستی"><button type="button" className="btn btn-success"  onClick = {() => this.acceptRequest(user.id)} ><img className="tick_sign" src={tickSign} alt="cross"/></button></abbr>
                        &nbsp;
                        <abbr title="رد پیشنهاد دوستی"><button type="button" className="btn btn-danger"  onClick = {() => this.rejectRequest(user.id)} ><img className="cross_sign" src={crossSign} alt="cross"/></button></abbr>    
                    </React.Fragment>
                    break;

                case 'stranger':
                    buttons = <button type="button" className="btn btn-success"  onClick = {() => this.doRequest(user.id)} >پیشنهاد دوستی</button> 
                    break;  
                    
                case 'being requested': 
                    buttons = <button type="button" className="btn btn-danger"  onClick = {() => this.cancelRequest(user.id)} >لغو پیشنهاد دوستی</button>  
                    break;  

                default:
                    buttons = null;
                      
            }
            return(<div className="users_paragraph" key={key}>
                        {user.name}&nbsp;
                        <img className="friends_image" src={user.image} alt="avatar"/>
                        <div>
                            {buttons}
                        </div>
                        {this.props.users.length !== (key+1) ? <hr className="users_seperator"/> : null}
                 </div>)
            
        });
            
        
        return (
            <React.Fragment>
                <nav id="navigation">
                    <ul>
                        <li><a href="/createpost">ایجاد پست جدید <img src= {plusSign} alt="plus-sign" className="create_sign"/></a></li>
                        <li>
                            <form className="user_search_form" onSubmit = {(event)=>this.searchClicked(event)}>
                                <button className="btn btn-info user_search_btn" type="submit" name="search">
                                    <img src={magnifierSign}  alt="magnifier" className="magnifier"/>
                                </button>
                                <input className="form-control" type="text" placeholder="جستجوی کاربران" name="users_name"/>
                            </form>
                        </li>
                        <li>
                            <button type="button" className="btn btn-info" id="dropdown_btn"><img src={dropdownImg} alt="dropdown_btn" id="dropdown_btn_img"/></button>
                        </li>
                            <li id='greeting'>سلام {this.props.user.name}</li>
                        <li><a href="/editprofile"><img src={this.props.user.image} alt="profile" id="profile_image"/></a></li>
                    </ul>
                </nav>


                <div className="toast show toast_users_box" role="alert" aria-live="assertive" aria-atomic="true" style={{display:'none', zIndex: '10000'}}>
                    <div className="toast-header">
                        <strong className="ml-auto">موارد یافته شده</strong>
                        <button type="button" className="ml-2 mb-1 close close_toast_users" data-dismiss="toast" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="toast-body toast_users_body">
                        {users.length===0 ? 'هیچ کاربری یافت نشد' : users}
                    </div>
                </div>
            </React.Fragment>    

        )
    }
}


Nav.propTypes = {
    users: PropTypes.array.isRequired,
    user: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    users: state.found_users.users,
    user: state.logged_in_user.user
})

export default connect(mapStateToProps, {searchUsers,getCurrentUser, doRequestCommand, cancelRequestCommand, acceptCommand, rejectCommand})(Nav);