import { combineReducers } from 'redux';
import navReducer from './navReducer';
import loggedinUserReducer from './loggedinUserReducer';
import advReducer from './advReducer';
import postsRedecer from './postsReducer';
import accordionsReducer from './accordionsReducer';

export default combineReducers({
    found_users: navReducer,
    logged_in_user: loggedinUserReducer,
    advContent: advReducer,
    posts_List: postsRedecer,
    accordionInfo: accordionsReducer

});