const initialState = {
    users: []
}

export default function(state = initialState, action){
    switch(action.type){

        case 'FETCH_USERS':
            return {
                ...state,
                users: action.payload
            }

        case 'ACCEPT_REQUEST':
            state.users = state.users.map(user=>{
                if(user.id===action.id){
                   user.friendship_state = "friend"
                   return user;
                }
                else{
                   return user;
                }
            })
            return {...state};
            

        case 'REJECT_REQUEST':
            state.users = state.users.map(user=>{
                if(user.id===action.id){
                   user.friendship_state = "stranger"
                   return user;
                }
                else{
                    return user;
                }
            })
            return {...state};
           

        case 'DO_REQUEST_COMMAND':
            state.users = state.users.map(user=>{
                if(user.id===action.id){
                   user.friendship_state = "being requested"
                   return user;
                }
                else{
                    return user;
                }
            })
            return {...state};     
           
        case 'CANCEL_REQUEST_COMMAND':
            state.users = state.users.map(user=>{
                if(user.id===action.id){
                   user.friendship_state = "stranger"
                   return user;
                }
                else{
                    return user;
                }
            })
            return {...state};
           
        default:
            return state;
    }

}