export const searchUsers = (keyword) => dispatch =>{
    fetch('/api/found-users/'+keyword,
        {
            method: 'GET',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json'
            }
        }
    )              
    .then(res=>{
        // it tells additional.js that fetch process is completed and showing toast_users_box is allowed
        document.getElementById('fetch_is_completed').innerHTML = 'true';
        // *********************************************************************************************
        return res.json()})
    .then(users=>dispatch({
        type: 'FETCH_USERS',
        payload: users
    }));
}

export const getCurrentUser = () => dispatch =>{
    fetch('/api/logged-in-user',
        {
            method: 'GET',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json'
            }
        }
    )              
    .then(res=>res.json())
    .then(user=>dispatch({
        type: 'GET_CURRENT_USER',
        payload: user
    }));
}

export const acceptCommand = (id) => dispatch =>{
    fetch('/api/friend-requests',
        {
            method: 'PUT',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({id: id, friendship_state: 'friend'})
        }
    );
    return dispatch({
        type: 'ACCEPT_REQUEST',
        id: id
    }); 
}

export const rejectCommand = (id) => dispatch =>{
    fetch('/api/friend-requests',
        {
            method: 'PUT',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({id: id, friendship_state: 'stranger'})
        }
    );
    return dispatch({
        type: 'REJECT_REQUEST',
        id: id
    });  
}

export const doRequestCommand = (id) => dispatch =>{
    fetch('/api/request',{
        method: 'PUT',
        headers: {
            'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({id: id, friendship_state: 'being requested'})
    });
    return dispatch({
        type: 'DO_REQUEST_COMMAND',
        id: id
    });
}

export const cancelRequestCommand = (id) => dispatch =>{
    fetch('/api/request',{
        method: 'PUT',
        headers: {
            'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({id: id, friendship_state: 'stranger'})
    });
    return dispatch({
        type: 'CANCEL_REQUEST_COMMAND',
        id: id
    });
}


