import React from 'react'
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {requestPageNumber} from '../actions/paginationAction'
class Pagination extends React.Component {

    pageClicked = (event, requestedPageNumber) =>{
        event.preventDefault();
        console.log(requestedPageNumber)
        window.scrollTo(0,0);
        this.props.requestPageNumber(requestedPageNumber);  
    }

    render() {
        let postsNumber = this.props.items.length;
        let pagination_list = null;
        let lists = [];
        let paginationNumber = null;
        if(postsNumber > this.props.ppp){
            
            paginationNumber = Math.ceil(postsNumber/this.props.ppp);

            for (let counter = 1; counter <= paginationNumber; counter++){
                lists.push(<li key = {counter} className={this.props.pageNumber === counter ? 'page-item active' : 'page-item'}>
                                <a className="page-link" href={`page-${counter}`} onClick={(event)=>this.pageClicked(event,counter)}>{counter}</a>
                            </li>);
            }
            pagination_list = (
                <ul className="pagination">
                        <li className={this.props.pageNumber===1 ? 'page-item disabled' : 'page-item'}>
                            <a className="page-link" href="<<" onClick={(event)=>this.pageClicked(event,this.props.pageNumber-1)}>&laquo;</a>
                        </li>
                            {lists}
                        <li className={this.props.pageNumber===paginationNumber ? 'page-item disabled' : 'page-item'}>
                            <a className="page-link" href=">>" onClick={(event)=>this.pageClicked(event,this.props.pageNumber+1)}>&raquo;</a>
                        </li>
                </ul>
            );
        }
        
        return (
            <div id="posts_pagination">
                {pagination_list}
            </div>
        )
    }
}


Pagination.propTypes = {
    items: PropTypes.array.isRequired,
    pageNumber: PropTypes.number.isRequired,
    ppp: PropTypes.number.isRequired
}

const mapStateToProps = state => ({
    items: state.posts_List.items,
    pageNumber: state.posts_List.pageNumber,
    ppp: state.posts_List.ppp
})


export default connect(mapStateToProps, {requestPageNumber})(Pagination);



