import React from 'react'
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { getAdvertisement } from '../actions/advActions'

class Advertisement extends React.Component {

    componentDidMount(){
        this.props.getAdvertisement();
    }

    render() {
        const advItems = this.props.items.map((item,key)=>{
            return (<div className="alert alert-dismissible advertisement_box" key={key}>
                <button type="button" className="close" data-dismiss="alert">&times;</button>
                <strong className = "advertisement_title">{item.title}</strong>
                <br/>
                    {item.content}
            </div>)
        });
        return (
            <React.Fragment>
                {advItems}
            </React.Fragment>
            
        )
    }
}

Advertisement.propTypes = {
    items: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
    items: state.advContent.items
})


export default connect(mapStateToProps, {getAdvertisement})(Advertisement);