const initialState = {
    friends: [],
    friend_requests: []
}

export default function(state = initialState, action){
    switch(action.type){

        case 'GET_FRIENDS':
            return {
                ...state,
                friends: action.payload
            }

        case 'GET_FRIEND_REQUESTS':
            return {
                ...state,
                friend_requests: action.payload
            }

        case 'REMOVE_FRIEND':
            state.friends =  [...state.friends.filter((item)=>item.id !== action.id )];
            return {...state};
           

        case 'ACCEPT_REQUEST':

            state.friend_requests.forEach(item => {
                if(item.id === action.id){
                    state.friends.push(item);
                }    
            });
            state.friend_requests =  [...state.friend_requests.filter(item=>item.id !== action.id )];

            return {...state}

        case 'REJECT_REQUEST':
            state.friend_requests =  [...state.friend_requests.filter(item=>item.id !== action.id )];

            return {...state}  
           
        default:
            return state;
    }

}