export const getFriends = () => dispatch =>{
    fetch('/api/friends',
        {
            method: 'GET',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json'
            
            }
        }
    )              
    .then(res=>{
        return res.json()})
    .then(users=>dispatch({
        type: 'GET_FRIENDS',
        payload: users
    }));
}


export const getFriendRequests = () => dispatch =>{
    fetch('/api/friend-requests',
        {
            method: 'GET',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json' 
            }
        }
    )              
    .then(res=>res.json())
    .then(users=>dispatch({
        type: 'GET_FRIEND_REQUESTS',
        payload: users
    }));
}


export const removeFriend = (id) => dispatch =>{
    fetch('/api/friends',
        {
            method: 'DELETE',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({id: id})
        }
    );
    return dispatch({
        type: 'REMOVE_FRIEND',
        id: id
    });
}

export const acceptRequest = (id) => dispatch =>{
    fetch('/api/friend-requests',
        {
            method: 'PUT',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({id: id, friendship_state: 'friend'})
        }
    );
    return dispatch({
        type: 'ACCEPT_REQUEST',
        id: id
    }); 
}    

export const rejectRequest = (id) => dispatch =>{
    fetch('/api/friend-requests',
        {
            method: 'PUT',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({id: id, friendship_state: 'stranger'})
        }
    );
    return dispatch({
        type: 'REJECT_REQUEST',
        id: id
    }); 
}







