import React from 'react'
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { getPosts } from '../actions/postsActions';
import { likePost } from '../actions/postsActions';
import { unlikePost } from '../actions/postsActions';
import { deletePost } from '../actions/postsActions';
import { getInterested } from '../actions/postsActions';
import unlikeSign from '../images/unlike.png';
import trashSign from '../images/trash-fill.svg';
class Posts extends React.Component {
    componentDidMount(){
        this.props.getPosts();
    }

    likePostClicked = (id) => {
        this.props.likePost(id);
    }

    unlikePostClicked = (id) => {
        this.props.unlikePost(id);
    }

    deletePostClicked = (id) => {
        if(window.confirm("آیا از حذف پست اطمینان دارید؟")){
            this.props.deletePost(id);
        }
    }
    badgeLinkClicked = (event,id) => {
        event.preventDefault();
        this.props.getInterested(id);

        
    }

    render() {
        let postsNumber = this.props.items.length;
        let slicedPosts = null;
        if(postsNumber <= this.props.ppp){
            slicedPosts = this.props.items
        }
        else{ 
            if(this.props.pageNumber*this.props.ppp>postsNumber){
                slicedPosts = this.props.items.slice((this.props.pageNumber-1)*this.props.ppp,postsNumber);
            }
            else{
                slicedPosts = this.props.items.slice((this.props.pageNumber-1)*this.props.ppp,this.props.pageNumber*this.props.ppp);
            }
        }
        const posts = slicedPosts.map((item, key)=>{

            let yourself_btns =
                <React.Fragment> 
                    {item.you_liked_it ? <abbr title="لغو پسند این پست"><button type="button" className="btn btn-danger unlike_btn" onClick = {()=>this.unlikePostClicked(item.id)}><img src={unlikeSign} alt="unlike"/></button></abbr>
                                       : <abbr title="پسندیدن این پست"><button type="button" className="btn btn-success like_btn" onClick = {()=>this.likePostClicked(item.id)} >♥️</button></abbr>}
                    &nbsp;&nbsp;
                    <a href={`/editpost/${item.id}`} className="badge-link"><abbr title="ویرایش این پست"><button type="button" className="btn btn-secondary edit_btn">✎</button></abbr></a>
                    &nbsp;&nbsp;
                    <abbr title="حذف این پست"><button type="button" className="btn btn-warning delete_btn" onClick = {()=>this.deletePostClicked(item.id)} ><img src={trashSign} alt="delete_btn" /></button></abbr>
                    
                </React.Fragment>   
            
            
                let post_btns = {
                    others: item.you_liked_it ? <abbr title="لغو پسند این پست"><button type="button" className="btn btn-danger unlike_btn" onClick = {()=>this.unlikePostClicked(item.id)}><img src={unlikeSign} alt="unlike"/></button></abbr> : <abbr title="پسندیدن این پست"><button type="button" className="btn btn-success like_btn" onClick = {()=>this.likePostClicked(item.id)}>♥️</button></abbr>,
                    yourself: yourself_btns
                }


            return(<div className="card mb-3" key={key}>
                    <h4 className="card-header">
                        <img className="user_image" src={item.profile_image} alt="uesr_image"/> &nbsp;
                        {item.name}
                    </h4>
                    <div className="card-body">
                      <h4 className="card-title text-info card_title_font">{item.title}</h4>
                    </div>
                    <img className="post_image" src={item.post_image} alt="Card"/>
                    <div className="card-body">
                      <p className="card-text text-success card-text-content">
                        {item.content}
                      </p>
                    </div>
                    <ul className="list-group list-group-flush post-list-group">
                      <li className="list-group-item">
                          <a href="interested_people" onClick={(event)=>this.badgeLinkClicked(event,item.id)} className="badge-link interested-link">
                            <span className="badge badge-success text-secondary badge-span">{item.likes}</span>
                           &nbsp; نفر پسندیده (اند)
                          </a>
                      </li>
                    </ul>
                    <div className="card-body">
                
                        {item.yourself ? post_btns.yourself : post_btns.others }
                      

                    </div>
                    <div className="card-footer text-muted bg-light">
                     <b className="text-secondary" style={{fontSize: '17px'}}>{item.post_date}</b>
                    </div>
                </div>
            );

        });
        const interested_users = this.props.interested_people.map((item,key)=>{
            return(<React.Fragment key= {key}>
                <div className="users_paragraph">
                    {item.name}&nbsp;
                    <img className="friends_image" src={item.image} alt="avatar" />
                </div>

                {this.props.interested_people.length !== (key+1) ? <hr className="users_seperator" /> : null}
            </React.Fragment>);
        })
        const interested_box = 
            (<div className="toast show toast_interested_box" role="alert" aria-live="assertive" aria-atomic="true" style={{display:'none', zIndex: '10000'}}>
                <div className="toast-header">
                    <strong className="ml-auto">مورد پسند کاربر(ان) زیر</strong>
                    <button type="button" className="ml-2 mb-1 close close_toast_interested" data-dismiss="toast" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="toast-body toast_users_body">
                    {interested_users.length===0? 'هیچ کس نپسندیده': interested_users}
                </div>
            </div>
            );


        return (
            <React.Fragment>
                {posts}
                {interested_box}
            </React.Fragment>
        )
    }
}

Posts.propTypes = {
    items: PropTypes.array.isRequired,
    interested_people: PropTypes.array.isRequired,
    pageNumber: PropTypes.number.isRequired,
    ppp: PropTypes.number.isRequired
}

const mapStateToProps = state => ({
    items: state.posts_List.items,
    interested_people: state.posts_List.interested_people,
    pageNumber: state.posts_List.pageNumber,
    ppp: state.posts_List.ppp
})


export default connect(mapStateToProps, {getPosts, likePost, unlikePost, deletePost, getInterested})(Posts);