export const getAdvertisement = () => dispatch =>{
    fetch('/api/advertisements',
        {
            method: 'GET',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json'
            
            }
        }
    )              
    .then(res=>res.json())
    .then(adv=>dispatch({
        type: 'ADVERTISEMENT_CONTENT',
        payload: adv 
    }));
}

