export const getPosts = () => dispatch =>{
    fetch('/api/posts',
        {
            method: 'GET',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json'
            
            }
        }
    )              
    .then(res=>res.json())
    .then(posts=>dispatch({
        type: 'POSTS_LIST',
        payload: posts 
    }));
}

export const likePost = (id) => dispatch =>{
    fetch('/api/posts',
        {
            method: 'POST',
            headers : { 
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'content-type': 'application/json'
            
            },
            body: JSON.stringify({id: id})
        });
    return dispatch({
        type: 'LIKE_POST',
        id: id
    });
}

export const unlikePost = (id) => dispatch =>{
    fetch('/api/posts/'+id,
        {
            method: 'PUT',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            }
        });
    return dispatch({
        type: 'UNLIKE_POST',
        id: id
    });
}

export const deletePost = (id) => dispatch =>{
    fetch('/api/posts/'+id,{
        method: 'DELETE',
        headers: {
            'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
            'Content-Type': 'application/json'
        }
    });
    return dispatch({
        type: 'DELETE_POST',
        id: id
    });
}

export const getInterested = (id) => dispatch =>{
    fetch('/api/interested/'+id,
        {
            method: 'GET',
            headers: {
                'X-CSRF-TOKEN': document.getElementsByName("csrf-token")[0].getAttribute("content"),
                'Content-Type': 'application/json'
            }
        }
    )              
    .then(res=>{ 
        // it tells additional.js that fetch process is completed and showing toast_interested_box is allowed
        document.getElementById('fetch_is_completed').innerHTML = 'true';
        // *********************************************************************************************
        return res.json()})
    .then(interested_people=>dispatch({
        type: 'INTERESTED_LIST',
        payload: interested_people 
    }));
}



